per styles */\n\n.calendarContainer .dayWrapper {\n  float: left;\n  width: 14.28571429%;\n  margin-top: 5px;\n  position: relative;\n}\n\n/* Day wrapper button styles */\n.calendarContainer .dayWrapper button {\n  border: none;\n  background: none;\n  outline: none;\n  width: 100%;\n  cursor: pointer;\n  width: 40px;\n  height: 40px;\n  border-radius: 50%;\n  font-size: 1.1em;\n  padding: 0;\n  line-height: 1.5;\n  padding: 0px 0px 1px 0px;\n}\n\n.jalaali.calendarContainer .dayWrapper button {\n  padding: 0px 0px 1px 0px;\n}\n\n.calendarContainer .dayWrapper:not(.selected) button:hover {\n  background-color: $hoverColor;\n}\n\n.calendarContainer .dayWrapper button[disabled] {\n  color: #aaa;\n  cursor: not-allowed;\n  background-color: #ebebeb;\n}\n\n.calendarContainer .dayWrapper button.selected {\n  background-color: #337ab7;\n  color: #ffffff;\n}\n\n.calendarContainer .dayWrapper:not(.currentMonth) button {\n  opacity: 0.5;\n}\n\n/* Days of week row */\n\n.calendarContainer .daysOfWeek {\n  margin-bottom: 5px;\n  padding-bottom: 5px;\n  display: flex;\n  width: 100%;\n  color: lighten($greycolor, 10%);\n}\n\n.calendarContainer .daysOfWeek > div {\n  flex-grow: 1;\n  justify-content: space-between;\n  width: 42px;\n}\n\n/* Month selector portion */\n\n.calendarContainer .monthsList {\n  clear: both;\n  width: 100%;\n}\n\n.calendarContainer .monthsList button {\n  width: 33.33333332%;\n  height: 25%;\n  float: right;\n  border: 1px solid #f9f9f9;\n  outline: none;\n  font-size: 1em;\n  background: #fff;\n  padding: 10px 0;\n  cursor: pointer;\n}\n\n.calendarContainer .monthsList button:hover {\n  background: #eeeeee;\n  cursor: pointer;\n}\n\n/* Year selector portion */\n\n.calendarContainer .yearsList {\n  clear: both;\n  width: 100%;\n  max-height: 200px;\n  overflow-y: scroll;\n}\n\n.calendarContainer .yearsList button {\n  width: 20%;\n  height: 25%;\n  float: right;\n  border: 1px solid #f9f9f9;\n  outline: none;\n  font-size: 1em;\n  background: #fff;\n  padding: 10px 0;\n  cursor: pointer;\n}\n\n.calendarContainer .yearsList button:hover {\n  background: #eeeeee;\n  cursor: pointer;\n}\n\n/* Selected state of buttons */\n\n.calendarContainer .selected button,\n.calendarContainer .selected button:hover :not([disabled]),\n.calendarContainer .selected button:active,\n.calendarContainer .selected button:focus {\n  background-color: $selectedColor;\n  color: #ffffff;\n}\n\n.calendarContainer.jalaali {\n  direction: rtl;\n}\n\n.calendarContainer.jalaali .dayWrapper {\n  float: right;\n}\n\n/*time picker style*/\n\n.time-picker-container {\n  margin-bottom: 10;\n}\n\n.time-picker-container > .time-label {\n  float: left;\n  line-height: 30px;\n  width: 50%;\n  text-align: center;\n}\n\n.time-picker-container > .time-picker-panel {\n  float: right;\n  width: 50%;\n}\n\n.time-picker-container.jalaali > .time-label {\n  float: right;\n}\n\n.time-picker-container.jalaali > .time-picker-panel {\n  float: left;\n}\n\n.rc-time-picker {\n  border-radius: 4px;\n  display: inline-block;\n  box-sizing: border-box;\n  width: 110px;\n  border: 1px solid #f7f7f7;\n  font-size: 1.2em;\n}\n\n.rc-time-picker * {\n  box-sizing: border-box;\n}\n\n.rc-time-picker-input {\n  margin: 4px 0;\n  padding: 0px 15px 1px 15px;\n  direction: ltr;\n  text-align: center;\n  width: 100%;\n  position: relative;\n  display: inline-block;\n  cursor: pointer;\n  font-size: 1em;\n  line-height: 1.5;\n\n  border: none;\n  background-image: none;\n  background-color: transparent;\n  -webkit-box-shadow: none;\n  -moz-box-shadow: none;\n  box-shadow: none;\n}\n\n.jalaali.calendarContainer .rc-time-picker-input {\n  padding: 0px 15px 3px 15px;\n}\n\n.rc-time-picker-input:focus {\n  box-shadow: none;\n  border: none;\n  background-color: #f2f2f2;\n}\n\n.rc-time-picker:hover {\n  background-color: #f2f2f2;\n}\n\n.rc-time-picker-panel {\n  z-index: 2001;\n  width: 170px;\n  position: absolute;\n  box-sizing: border-box;\n}\n\n.rc-time-picker-panel * {\n  box-sizing: border-box;\n}\n\n.rc-time-picker-panel-inner {\n  display: inline-block;\n  position: relative;\n  outline: none;\n  list-style: none;\n  font-size: 12px;\n  text-align: left;\n  background-color: #fff;\n  border-radius: 3px;\n  box-shadow: 0 1px 5px #ccc;\n  background-clip: padding-box;\n  border: 1px solid #ccc;\n  line-height: 1.5;\n}\n\n.rc-time-picker-panel-input {\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  cursor: default;\n  line-height: 1.5;\n  outline: 0;\n  border: 1px solid transparent;\n  padding: 4px 0;\n  font-size: 1.4em;\n  text-align: center;\n  font-family: inherit;\n}\n\n.rc-time-picker-panel-input,\n.rc-time-picker-panel-input:hover {\n  box-shadow: none;\n  border: none;\n}\n\n.rc-time-picker-panel-input-wrap {\n  box-sizing: border-box;\n  position: relative;\n  padding: 6px;\n  border-bottom: 1px solid #e9e9e9;\n}\n\n.rc-time-picker-panel-input-invalid {\n  border-color: red;\n}\n\n.rc-time-picker-panel-clear-btn {\n  position: absolute;\n  right: 6px;\n  cursor: pointer;\n  overflow: hidden;\n  width: 20px;\n  height: 20px;\n  text-align: center;\n  line-height: 20px;\n  top: 6px;\n  margin: 0;\n}\n\n.rc-time-picker-panel-clear-btn:after {\n  content: 'x';\n  font-size: 12px;\n  color: #aaa;\n  display: inline-block;\n  line-height: 1;\n  width: 20px;\n  transition: color 0.3s ease;\n}\n\n.rc-time-picker-panel-clear-btn:hover:after {\n  color: #666;\n}\n\n.rc-time-picker-panel-select {\n  float: left;\n  font-size: 12px;\n  border: 1px solid #e9e9e9;\n  border-width: 0 1px;\n  margin-left: -1px;\n  box-sizing: border-box;\n  width: 56px;\n  overflow: hidden;\n  position: relative;\n}\n\n.rc-time-picker-panel-select-active {\n  overflow-y: auto;\n}\n\n.rc-time-picker-panel-select:first-child {\n  border-left: 0;\n  margin-left: 0;\n}\n\n.rc-time-picker-panel-select:last-child {\n  border-right: 0;\n}\n\n.rc-time-picker-panel-select ul {\n  list-style: none;\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  max-height: 144px;\n  overflow-x: hidden;\n  overflow-y: scroll;\n}\n\n.rc-time-picker-panel-select li {\n  list-style: none;\n  box-sizing: content-box;\n  margin: 0;\n  padding: 0 0 0 16px;\n  width: 100%;\n  height: 24px;\n  line-height: 24px;\n  text-align: left;\n  cursor: pointer;\n  user-select: none;\n}\n\n.rc-time-picker-panel-select li:hover {\n  background: #edfaff;\n}\n\nli.rc-time-picker-panel-select-option-selected {\n  background: #edfaff;\n  color: #2db7f5;\n}\n\nli.rc-time-picker-panel-select-option-disabled {\n  color: #bfbfbf;\n}\n\nli.rc-time-picker-panel-select-option-disabled:hover {\n  background: transparent;\n  cursor: not-allowed;\n}\n\n.tether-element.tether-element-attached-top.tether-element-attached-center.tether-target-attached-bottom.tether-target-attached-center.tether-enabled {\n  z-index: 2000;\n}\n\n.calendarContainer *,\n.datepicker-input {\n  font-family: inherit;\n}\n\n.today {\n  button {\n    border: 3px solid #4285f4;\n  }\n}\n\n.highlighted {\n  background-color: #e7f1f2;\n  &.rangestart {\n    background: linear-gradient(90deg, #e7f1f2 50%, transparent 50%);\n    // button {\n    //   background: white !important;\n    //   border: 1px solid #4285f4 !important;\n    // }\n  }\n  &.rangeend {\n    background: linear-gradient(90deg, transparent 50%, #e7f1f2 50%);\n    button {\n      background: white !important;\n      border: 1px solid #4285f4 !important;\n    }\n  }\n}\n\n.jalaali.calendarContainer {\n  .selectToday {\n    padding: 4px 0 6px 0;\n  }\n}\n\n.calendarButton {\n  display: block;\n  width: 100%;\n  background: #4285f4;\n  color: #fff;\n  outline: none;\n  border-radius: 5px;\n  border: 0;\n  cursor: pointer;\n  padding: 5px 0 7px 0;\n  transition: 0.2s all ease-in-out;\n  transition-property: background;\n\n  &:hover {\n    background: darken(#4285f4, 10);\n  }\n}\n\n.toggleButton {\n  margin-bottom: 1rem;\n}\n\n.selectToday {\n  margin-top: 1rem;\n}\n\n.highLightDot-container {\n  text-align: center;\n  bottom: 0;\n