;\n              margin: 0 8px;\n            }\n          }\n        }\n      }\n\n      .highLightDot-container {\n        position: absolute;\n        bottom: 0;\n        right: 0;\n      }\n    }\n  }\n\n  .selected button.day-button,\n  .selected button.day-button:hover :not([disabled]),\n  .selected button.day-button:active,\n  .selected button.day-button:focus {\n    background-color: unset;\n    color: unset;\n\n    .day-number {\n      background-color: $selectedColor !important;\n      color: #ffffff !important;\n    }\n  }\n}\n"],"sourceRoot":""}]);
